<template>
  <div style="max-width: 300px; width: 100%;">
    <h1 class="text-center">Sign up</h1>
    <router-link
      to="/"
      class="float-left"
    >
      ðŸ¡„  Go back
    </router-link>
    <br>
    <form class="mt-4 w-100">
      <div class="form-group">
        <label for="signupInputEmail">
          Email:
        </label>
        <input
          v-validate="'required|' + validatorRules.email"
          id="signupInputEmail"
          v-model="form.email"
          :error-messages="errors.collect('email')"
          :class="{
            'form-control': true,
            'is-invalid': errors.has('email')
          }"
          data-vv-name="email"
          type="email"
          class="form-control"
          placeholder="Enter email"
        >
        <div :class="{ 'validation-feedback': true, 'invalid-feedback': errors.has('email') }">
          {{ errors.first('email') }}
        </div>
      </div>
      <div class="form-group">
        <label for="signupInputName">
          Name:
        </label>
        <input
          v-validate="validatorRules.name"
          id="signupInputName"
          v-model="form.name"
          :error-messages="errors.collect('name')"
          :class="{
            'form-control': true,
            'is-invalid': errors.has('name')
          }"
          data-vv-name="name"
          type="text"
          class="form-control"
          placeholder="Enter name"
        >
        <div :class="{ 'validation-feedback': true, 'invalid-feedback': errors.has('name') }">
          {{ errors.first('name') }}
        </div>
      </div>
      <div class="form-group">
        <label for="signupInputPassword">
          Password:
        </label>
        <input
          v-validate="validatorRules.password"
          id="signupInputPassword"
          v-model="form.password"
          :class="{
            'form-control': true,
            'is-invalid': errors.has('password')
          }"
          data-vv-name="password"
          type="password"
          class="form-control"
          placeholder="Password"
        >
        <div :class="{ 'validation-feedback': true, 'invalid-feedback': errors.has('password') }">
          {{ errors.first('password') }}
        </div>
      </div>
      <button
        :disabled="!!errors.items.length"
        type="submit"
        class="btn btn-primary float-right"
        @click.prevent="submit"
      >
        <span v-if="loading">
          Loading...
        </span>
        <span v-else>
          Sign up
        </span>
      </button>

      <span class="ml-1">
        or
        <router-link :to="{ name: 'signin' }">Sign in</router-link>
      </span>
    </form>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import validatorMixin from '@/tools/mixins/validator'

export default {
  name: 'Signup',
  mixins: [validatorMixin],
  data: () => ({
    form: {
      name: 'Vasya Pupkin',
      email: 'vasya@test.com',
      password: 'password'
    },
    loading: false
  }),
  methods: {
    async submit () {
      if (await this.validateByMixin(this.form)) {
        this.loading = true
        try {
          await this.signup(this.form)
        } finally {
          this.loading = false
        }
      }
    },
    ...mapActions('auth', ['signup'])
  },
  head () {
    return {
      title: 'Sign Up Page',
      meta: [
        { content: 'Sign Up Page Description', name: 'description', hid: 'description' }
      ]
    }
  }
}
</script>
